import moveVerify from "@/components/moveVerify.vue"

import {
	mapMutations
} from 'vuex';

export default {
	components: {
		"move-verify": moveVerify
	},

	data() {
		return {
			mobile: '',
			resultData: {},
			verifyCode: '',
			logining: false
		}
	},
	onLoad() {
		console.log('login页面onLoad');
	},
	methods: {
		...mapMutations(['login']),
		/* 校验结果回调函数 */
		verifyResult(res) {
			this.checkExistance();
			console.log(res);
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset() {
			this.$refs.verifyElement.reset();

			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		toRegist() {
			this.$api.msg('去注册');
			uni.navigateTo({
				url: '/pages/public/register',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toLoginByName() {
			uni.navigateTo({
				url: '/pages/public/loginByName',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toForgetPwd() {
			uni.navigateTo({
				url: '/pages/public/forgetPwd',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		checkExistance() {
			if (this.$refs.verifyElement.phoneExistance == true) {
				this.$api.msg("短信验证码已发送" + this.mobile)
			} else {
				this.$api.msg("手机号有误或未注册")
				// this.verifyReset()
				// setTimeout(() => {
				// 	this.toRegist()
				// }, 3000)

			}
		},
		async toLogin() {
			
			
			const {
				mobile,
				verifyCode
			} = this;
			/* 数据验证模块 */
			uni.request({
				url: this.apiServer + '/customer/loginByPhone',
				method: 'POST',
				dataType: "json",
				data: {
					"mobile": this.mobile,
				},
				success: (res) => {
					const result = res.data
					console.log(result[0])
					//const result = await this.$api.json('userInfo');
					if(result.status == 500){
						this.$api.msg('网络错误');
					}
					if (result[0].phone == this.mobile) {
						this.login(result[0]);
						this.logining = true;
						uni.reLaunch({
							url: "/pages/user/user",
							success: res => {}
						});
					} else {
						this.$api.msg("验证码错误");
						this.logining = false;
					}
				},
				fail: () => {
					this.$api.msg('网络错误');
				},
			});

		}
	},

}
